@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lora:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

#root {
  margin: 0 auto;
  max-width: 1400px;
  padding: 0 25px; }

.header .header-links-container {
  display: flex;
  justify-content: space-around;
  margin: 20px 0; }

.header .header-links {
  height: 35px;
  text-align: center;
  border: none;
  font-size: 16px;
  text-transform: uppercase;
  text-decoration: underline;
  cursor: pointer;
  padding: 0 20px;
  background: none; }

.header .header-name {
  text-align: center;
  font-family: 'Lobster', cursive;
  font-size: 30px; }

.header hr {
  border-color: #d3d6d2; }

@media only screen and (min-width: 768px) {
  .header .header-name {
    font-size: 40px; }
  .header .header-links-container {
    margin: 20px 0px; }
  .header .header-links {
    font-size: 18px;
    width: 30%; } }

.content .left-content,
.content .right-content {
  text-align: center; }

.content .left-content {
  margin-bottom: 30px;
  font-family: 'Lora', cursive; }

@media only screen and (min-width: 768px) {
  .content .container {
    display: flex;
    height: 100%;
    align-items: center; }
  .content .left-content,
  .content .right-content {
    width: 50%; }
  .content .left-content {
    margin-bottom: 0; }
  .content .home-image {
    width: 75%;
    max-width: 430px; } }

.resume {
  width: 100%;
  height: 100%; }

.card {
  border: 1px solid black;
  margin-bottom: 20px;
  border-radius: 10px;
  width: 100%;
  font-size: 16px;
  overflow-x: hidden;
  font-family: 'Lora', cursive; }
  .card img {
    width: 100%; }
  .card button {
    padding: 0; }
  .card .item {
    margin-bottom: 2%;
    padding: 5px; }
  .card .label {
    font-weight: 700;
    margin-right: 5px; }
  .card .image-container {
    display: flex;
    overflow: hidden;
    cursor: pointer; }
  .card .image-wrapper {
    position: relative;
    overflow: hidden; }
  .card .left-arrow,
  .card .right-arrow {
    position: absolute;
    height: 100%;
    background: none;
    border: none;
    cursor: pointer; }
  .card .right-arrow {
    top: 0;
    right: 0; }
  .card .arrow-icon {
    width: 50px;
    height: 100%; }
  .card .left-arrow-mask,
  .card .right-arrow-mask {
    position: absolute;
    background: grey;
    opacity: 0.3;
    height: 100%;
    width: 100%; }
  .card .left-arrow-mask {
    top: 0;
    left: 0; }
  .card .right-arrow-mask {
    top: 0;
    right: 0; }
  .card .template {
    width: 100%;
    height: 70%;
    overflow: hidden; }
  .card .svg {
    width: 100%;
    height: 100%; }
  @media only screen and (min-width: 768px) {
    .card {
      width: 49%;
      flex-wrap: wrap; } }

@media only screen and (min-width: 768px) {
  .portfolio-card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; } }

.footer {
  border-top: 1px solid #d3d6d2; }
  .footer .footer-links-container {
    display: flex;
    justify-content: center;
    margin: 10px; }
  .footer img {
    width: 30px; }
  .footer a {
    margin-right: 5%;
    padding: 0 20px; }
    .footer a:last-child {
      margin-right: 0; }

html, body, #root {
  height: 100%; }

#root {
  display: flex;
  flex-direction: column; }

.header,
.footer {
  flex-shrink: 0; }

.content {
  flex: 1 0 auto; }

